.container {
  padding: 0;
  margin-top: 20px;
  text-align: center;
  min-height: 95vh;
  position: relative;
}

.dark {
  background-color: #2F4F4F;
  color: white;
}

.light {
  background-color: #a2b9bc;
  color: #282c34;
}

.colorful {
  background: #808080; 
  background: -webkit-linear-gradient(to left, #3fada8, #808080);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #3fada8, #808080); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

