header {
  margin: 0;
  padding: 0;
  margin-bottom: -25px;
  text-align: center;
  background-color: orange;
}

header button {
  color: black;
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  width: 100px;
  margin: 30px 15px;
  padding: 10px 20px;
  vertical-align: middle;
  background-color: white;
}

header button:hover {
  background-color: purple;
}

header .selected {
  background-color: #2c3e50;
}