ul {
  margin-top: 2em;
  width: 780px;
  max-height:400px;
  overflow-y:auto;/*Also...*/
  overflow-x:hidden;/*And the end of the important part*/
  margin: 0px;
  border: 1px solid purple;
  padding: 0 2em;
}

.user-input,
.user-info{
  margin-top: 2em;
  border-top: 1px solid purple;
}

.main {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.user-input button,
.user-info button {
  font-size: 16px;;
  margin: 10px;
  padding: 5px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border: solid 2px;
  color: inherit;
}

