li {
  list-style: none;
  font-size: 20px;
  margin: 2px auto;
  text-align: center;
  border-bottom: 1px dotted white;
  padding-bottom: 1em;
  color: white;
}

li:last-of-type {
  border-bottom: none;
}

span {
  cursor: pointer;
}

input {
  background-color: #f6f6f6;
  border: none;
  color: #0d0d0d;
  padding: 5px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 5px;
  border: 2px solid #f6f6f6;
  border-radius: 5px 5px 5px 5px;
}

button {
  border: none;
  background-color: Transparent;
  margin: 0 10px;
  color: inherit;
}

button:hover {
  cursor: pointer;
}

.done {
  color: yellow;
  font-size: 20px;
  text-decoration: line-through;
}

.redo {
  color: green;
  font-size: 20px;
}

.delete {
  color: orange;
  font-size: 20px;
}