h1 {
  margin: 0 0 0.25em;
  color: white;
}

select {
  color: white;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  outline: 0;
  box-shadow: none;
  border: 0 !important;
  background: #2c3e50;
  width: 53em;
  height: 3em;
  line-height: 3;
  font-size: 1em;
  font-weight: 600;
  background: #2c3e50;
  overflow: hidden;
  border-radius: .25em;
  padding: 0 5em;
  margin-bottom: 2em;
}