.login {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.login input {
  border: none;
  padding: 1em 2em;
  text-align: center;
  display: inline-block;
  font-size: 1em;
  color: purple;
  font-weight: 600;
  background-color: #f6f6f6;
}

.login button {
  font-size: 1em;;
  padding: 1em 3em;
  text-align: center;
  display: inline-block;
  color: purple;
  font-weight: 600;
  background-color: #f6f6f6;
}